<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/shopLevel/list"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
       <li>带 * 为必填项</li>
       <li>设置店铺等级相关信息</li>
       <li>信誉值可以决定店铺的优先级，信誉值越高优先级越高</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px"  @submit.native.prevent>
        <el-form-item label="等级名称" prop="levelName" :error='errorMessage.levelName'>
          <el-input v-model="formData.levelName" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="等级图标" prop="iconId" :error='errorMessage.iconId' class="brands-logo">
          <cms-picture-zone v-model="formData.iconId" :data='formData.resourcesSpaceData' v-if="status"> 
          </cms-picture-zone>
        </el-form-item>
        <el-form-item label="商品发布数上限" prop="releaseMaxNum" :error='errorMessage.releaseMaxNum'>
          <el-input v-model="formData.releaseMaxNum" :autofocus="true"  maxlength="6"></el-input>
          <div class='gray'>0表示无上限</div>
        </el-form-item>
        <el-form-item label="图片空间数量上限" prop="uploadMaxNum" :error='errorMessage.uploadMaxNum'>
          <el-input v-model="formData.uploadMaxNum" :autofocus="true"  maxlength="6"></el-input>
          <div class='gray'>0表示无上限</div>
        </el-form-item>
        <el-form-item label="成长值" prop="exp" :error='errorMessage.exp'>
          <el-input v-model="formData.exp"  maxlength="6"></el-input>
          <div class='gray'>升级该店铺等级需要满足的成长值:，由买家在订单交易成功后评价产生的成长值:</div>
        </el-form-item>
        <el-form-item label="排序"  prop="sortNum" :error='errorMessage.sortNum'>
          <el-input v-model="formData.sortNum"  maxlength="2"></el-input>
          <div class='gray'>输入0~99之间的整数</div>
        </el-form-item>
        <el-form-item label="说明" prop="remark"  :error='errorMessage.remark'>
          <el-input v-model="formData.remark" type="textarea" :rows="6" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update"  v-color >保存</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { gradeGet, gradeUpdate } from '@/api/shop/shopLevel.js'

var defaultData = {
  'id': '',
  'levelName': '',
  'iconId': '',
  'releaseMaxNum': '',
  'uploadMaxNum': '',
  'moeny': '',
  'exp': '',
  'sortNum': '',
  'remark': ''
}
export default {
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        levelName: [rules.required('请填写等级名称'), rules.len(1, 150)],
        iconId: [rules.required('请上传等级图标', 'change')],
        releaseMaxNum: [rules.required('请填写商品发布数量上限值'), rules.number()],
        uploadMaxNum: [rules.required('请填写图片空间数量上限值'), rules.number()],
        moeny: [rules.required('请填写升级费用'), rules.number()],
        exp: [rules.required('请填写成长值'), rules.number()],
        sortNum: [rules.required('请填写排序值'), rules.number()],
        remark: [rules.len(1, 500)]
      }
    }
  },
  methods: {
    rest() { // 修改表单重置
      this.$refs.form.resetFields()
      this.destroyed()
      this.getFormData(this.id)
    },
    getFormData(id) { // 通过id获取表单信息
      gradeGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    update() {
      this.updateForm(gradeUpdate, this.formData, '/shopLevel/list', 'list')
    },
    reset() {
      this.status = false
      this.$nextTick(() => {
        this.status = true
      })
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
    }
  },
  created() {
    this.getFormData(this.id)
  }
}
</script>




